var USDAModule = (function() {

	let term = document.querySelector('#term');
	
	const search = document.querySelector('#search');

	search.addEventListener('click', function(e) {
		let searchTerm = term.value;
		searchUSDA(searchTerm);
	})

	const searchUSDA = function (term) {
		console.log('searchUSDA()', term)

		const API_KEY = 'r2bvhj9kL6I2OWmgp13rWCJ4NYD0FGaf7YckDNE5';
		let url = 'https://api.nal.usda.gov/ndb/list?format=json';

		axios.get(url, {
			params: {
				'key': API_KEY,
				// '_ep': search,
				'lt' : 'f',
				'sort': 'n'
			}
		})
		.then(function (results) {
			console.log(results)
		})

	}

})();